main {
    .yt-cta {

        @include media('>=small') {
            .youtube-video {
                margin-top: $measure--threequarters;
            }
        }

        @include media('>=tablet') {
            display: flex;
            grid-column: 2/-2;
            .yt-cta__heading {
                text-align: center;
            }
            .yt-cta__text, .yt-cta__video {
                flex: 1;
            }
            .yt-cta__text {
                margin-right: $measure--small2;
            }
        }
    
        @include media('<tablet') {
            grid-column: 1/-1;
            @include default-padding;
            .yt-cta__video {
                margin-top: $measure--small2; 
            }
        }
    
        // Generic
        margin: $measure--basic 0 $measure--small4 0;
        flex-wrap: wrap;
        .yt-cta__heading {
            flex-basis: 100%;
            margin-bottom: $measure--threequarters;
        }
        .yt-cta__text {
            .cta-block {
                margin: 0 0 $measure--small4;
            }
        }
    }
}
